@import url(https://fonts.googleapis.com/css?family=Karla&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  font-size: 12px;
}
* {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
  font-family: "Karla", sans-serif;
}
input:focus,textarea:focus{
  outline: none;
}
.app {
  min-height: 100vh;
}
@media screen and (min-width: 600px) {
  :root {
    font-size: 14px;
  }
}
@media screen and (min-width: 768px) {
  :root {
    font-size: 16px;
  }
}

.footer {
  height: 20vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 7fr 3fr;
  padding: 0 5%;
  background-color: #333;
}
.logo-wrapper {
  display: flex;
  align-items: center;
}
.logo-image {
  max-height: 60%;
}
.social-wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.social-wrapper a {
  margin-right: 10px;
}
.social-wrapper img {
  height: 5vh;
}
.copyright {
  grid-column: span 2;
  font-size: 0.7rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 992px) {
  .footer {
    height: 10vh;
    grid-template-columns: 3fr 4fr 3fr;
    grid-template-rows: 1fr;
    grid-template-areas: ". c .";
  }
  .logo-image {
    max-height: 100%;
  }
  .copyright {
    grid-column: span 1;
    grid-area: c;
    align-items: flex-end;
    padding-bottom: 10px;
  }
}
.header {
  height: 20vh;
  background-color: #333;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: 7fr 3fr;
  grid-template-areas: 'menu menu''search icon';
  padding: 0 5%;
}
.header > div {
  display: flex;
  align-items: center;
}
.login-wrapper {
  justify-content: flex-end;
  position: relative;
  grid-area: icon
}
.login-wrapper img {
  max-height: 6vh;
}
.login-form {
  width: 70vw;
  position: absolute;
  top: 110%;
  right: -3vw;
  background-color: #333;
  z-index: 1;
}
.login-form form {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login-form form input {
  height: 6vh;
  width: 80%;
  margin: 10px 0;
  border: 0;
  padding-left: 10px;
  background-color: #fff;
}
.login-form input[type='submit'] ,.logout-wrapper input{
  width: 80%;
  height: 6vh;
  background-color: #ffa500;
  color: #333;
  border:none;
  cursor: pointer;
}
.login-form input[type='submit']:hover ,.logout-wrapper input:hover{
  background-color:#e59400;

}
.logout-wrapper{
    height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-message{
  color: #ffa500;
}
.show{
  visibility: visible
}
.hide{
  visibility: hidden;
}
.header-links {
grid-area: menu;
}
.header-links a {
  font-size: 1.3rem;
  text-decoration: none;
  color: #ffa500;
  margin-right: 20px;
  cursor: pointer;
}
.header-links {
  justify-content: flex-start;
}
.header-searchbox-wrapper {
  justify-content: flex-start;
}
.header-searchbox {
    grid-area: search;
  width: 100%;
  height: 60%;
  position: relative;
  display: flex;
  align-items: center;
}
.header-searchbox input {
  height: 100%;
  width: 100%;
  padding: 10px 50px 10px 10px;
  font-size: 1.1rem;
  border: none;
}
.icon-wrapper {
  width: 40px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  right: 0;
  background-color: #ffa500;
}
.header-searchbox a {
  font-size: 1.2rem;
  color: #333;
  font-weight: bold;
}
@media screen and (min-width: 600px) {
  .header-searchbox {
    width: 60%;
  }
}
@media screen and (min-width: 768px) {
  .header-searchbox {
    width: 40%;
  }
  .login-form{
      width: 40vw;
  }
}
@media screen and (min-width: 992px) {
  .header {
    height: 10vh;
    grid-template-columns: 3fr 6fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: 'search menu icon'
  }
  .header-searchbox {
    width: 70%;
    height: 50%;
  }
  .header-links {
    justify-content: flex-end;
  }
  .header-links a {
    margin-left: 20px;
    margin-right: 0;
  }
  .login-form{
    width: 30vw;
    top: 100%
}

}

.headers {
  display: grid;
  grid-gap: 10px;
  padding: 5% 5%;
}
.headers-text {
  color: #333;
}
.text-title {
  margin: 10px 0;
  font-size: 2rem;
  font-weight: bold;
}
.text-content {
  font-size: 1.2rem;
}
.headers-image img {
  width: 80vw;
}
.categories-button {
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.categories-button a {
  padding: 10px 20px;
  background-color: #ffa500;
  color: #333;
  font-size: 1.5rem;
  font-weight: bold;
}
.categories-button a:hover {
  background-color: #e59400;
}
.title-wrapper {
  height: 10vh;
  background-color: #333;
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  color: #fff;
  padding: 0 5%;
}
.category-tickets-wrapper {
  display: grid;
  padding: 5% 5%;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: 1fr;
  grid-gap: 10px;
}
.category-ticket {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #333;
}
.category-ticket:hover p {
  background-color: #333;
  color: #ffa500;
  -webkit-transition: background-color 0.3s linear;
  transition: background-color 0.3s linear;
}
.category-image {
  width: 30vw;
}
.category-ticket p {
  padding: 10px 0;
  background-color: #ffa500;
  width: 100%;
  color: #333;
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  -webkit-transition: background-color 0.3s linear;
  transition: background-color 0.3s linear;
}
.about-content {
  display: flex;
  flex-direction: column-reverse;
  padding: 5% 5%;
}
.about-text {
  flex: 1 1;
  font-size: 1.2rem;
  color: #333;
  padding: 20px 0;
}
.about-image-wrapper {
  flex: 1 1;
}
.about-image-wrapper img {
  width: 80vw;
}
.form-wrapper {
  padding: 5% 5%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.message-delivered{
  background-color: #ffa500;
  color:#333;
  font-size: 1.5rem;
}
.form-wrapper form {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.form-wrapper form input,
.form-wrapper form textarea {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid #333;
  font-size: 1.1rem;
}
.form-wrapper form input[type="submit"] {
  background-color: #ffa500;
  color: #333;
  font-weight: bold;
  border: none;
}

.form-wrapper form input[type="submit"]:hover {
  background-color: #e59400;
  cursor: pointer;
}
.form-wrapper form textarea {
  resize: none;
  height: 12vh;
}
.form-message{
  font-size: 1.3em;
  color: #ffa500;
  visibility: hidden;
}
.show{
  visibility: visible
}
@media screen and (min-width: 768px) {
  .headers {
    grid-template-columns: 6fr 4fr;
    grid-template-rows: 7fr 3fr;
    grid-template-areas: "t i" "c i";
  }
  .headers-image {
    grid-area: i;
  }
  .headers-text {
    grid-area: t;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .categories-button {
    grid-area: c;
    height: 100%;
    justify-content: flex-start;
  }
  .headers-image img {
    width: 100%;
  }
  .category-tickets-wrapper {
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 20px;
  }
  .category-image {
    width: 20vw;
  }

  .about-content {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .about-text p {
    width: 60%;
  }
  .about-image-wrapper {
    display: flex;
    justify-content: flex-end;
  }
  .about-image-wrapper img {
    width: 30vw;
  }
  .form-wrapper form input[type="submit"] {
    width: 30%;
    align-self: flex-end;
  }
}
@media screen and (min-width: 992px) {
  .headers {
    grid-template-columns: 5fr 5fr;
    grid-auto-rows: 8fr 2fr;
  }
  .headers-text {
    width: 60%;
  }
  .category-tickets-wrapper {
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
  }
  .category-image {
    width: 12vw;
  }

  .form-wrapper form {
    width: 50%;
  }
}
.category {
  display: grid;
}
.category-name {
  display: flex;
  align-items: center;
  font-size: 2.5rem;
  text-transform: uppercase;
  color: #ffa500;
  background-color: #333;
  padding: 0 5%;
  height: 10vh;
}
.recomendation-wrapper {
  display: flex;
  flex-direction: column;
  padding: 0 5%;
}
.recomendation-wrapper > p {
  font-size: 1.4rem;
  padding: 10px 0;
}
.recomendation-wrapper > div {
  width: 50vw;
}
.search-box {
  height: 10vh;
  padding: 5% 5%;
  display: flex;
  align-items: center;
  border-top: 1px solid #333;
}
.search-box input {
  height: 6vh;
  width: 50vw;
  border: 1px solid #333;
  font-size: 1.1rem;
  padding: 0 10px;

}
.meals {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 5% 5%;
  justify-items: center;
  align-items: center;
  grid-gap: 20px;
}
.meals a {
  width: 100%;
}
.category-nothing-found{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.5rem;
  font-weight: bold;
  color: #333;
  grid-column: span 2;
}
@media screen and (min-width: 768px) {
  .meals {
    grid-template-columns: repeat(3, 1fr);
  }

  .recomendation-wrapper > div {
    width: 30vw;
  }
  .category-name,
  .recomendation-wrapper {
    padding: 2% 5%;
  }
}
@media screen and (min-width: 992px) {
  .meals {
    grid-template-columns: repeat(5, 1fr);
  }
  .recomendation-wrapper > div {
    width: 20vw;
  }
}

.single-meal-wrapper{
    display: flex;
    flex-direction: column;
    padding: 10% 0;
}
.single-meal-wrapper img{
    width: 100%;
    border: 2px solid #333;

}
.single-meal-wrapper p{
    height: 6vh;
    width: 100%;
    background-color: #333;
    color: #ffa500;
    font-size: 1rem;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    -webkit-transition: background-color 0.5s linear;
    transition: background-color 0.5s linear;
    font-weight: bold;

}
.single-meal-wrapper:hover p{
    color: #333;
    background-color: #ffa500;
    -webkit-transition: background-color 0.5s linear;
    transition: background-color 0.5s linear
}
@media screen and (min-width:768px){
    .single-meal-wrapper p{
        height: 7vh
    }
}




.single-meal{
  display: grid;
}
.single-meal-title {
  display: flex;
  align-items: center;
  font-size: 1.8rem;
  text-transform: uppercase;
  color: #ffa500;
  background-color: #333;
  padding: 0 5%;
  height: 10vh;
}
.recipe{
  padding: 5% 5%;
  display: grid;
  grid-gap: 10px
}
.single-meal-image img{
  width: 70%;

}
.recipe div{
  display: grid;
  grid-gap: 10px;
}
.recipe b{
  margin-right: 5px;
}
.recipe>div>p{
  font-size: 1.1em;
}
.recipe>div>p>a{
  color: #ffa500;
}
.measures{
  display: flex;
  justify-content: flex-start;
  padding: 5% 5%;
  background-color: #333
}
.measures table{
  width: 60%;
  color:#fff
}
table thead tr th{
  padding: 5px 0;
  border-bottom: 1px solid #ffa500;
  color: #ffa500;
  text-align: start
}
td{
  padding: 5px 0
}
.similar-meals-wrapper{
  display: grid;
  padding: 5%;
  justify-items: center;
  align-items: center;
  grid-gap: 20px;
}
.similar-meals-wrapper a{
  width: 60%;
  padding: 5% 0;
}
@media screen and (min-width:768px){

.similar-meals-wrapper a{
  width: 80%;
  
}
.similar-meals-wrapper{
  grid-template-columns: repeat(3,1fr);
}
}
@media screen and (min-width:992px){
  .single-meal-image img {
    width: 70%;
  }
  .recipe{
    grid-template-columns: 1fr 2fr
  }
  .measures table {
    width: 30%;
}
}
